import React from "react"
import { Link } from "gatsby"
import { useStaticQuery, graphql } from "gatsby"

import PNNLLogo from "../../static/pnnl_logo2.png"
import { rhythm, scale } from "../utils/typography"

const Layout = ({ location, title, children, copyright }) => {
  // TODO: better to query elsewhere?
  const data = useStaticQuery(graphql`
    query DescriptionLongQuery {
      site {
        siteMetadata {
          descriptionLong
        }
      }
    }
  `)
  const rootPath = `${__PATH_PREFIX__}/`
  const isRootPath = location.pathname === rootPath

  return (
    <>
      <header>
        <div className="header-title">
          <div className="header-item-inner">
            <div id="pnnl-logo" >
              <a href={copyright.url}><img alt="PNNL Logo" src={PNNLLogo} style={{marginTop: rhythm(isRootPath? 1.5 : 0), height: rhythm(isRootPath ? 1.7 : 1.25)}}/></a>
            </div>
            <h1 style={{ ...scale(isRootPath ? 1.5 : 1), paddingTop: rhythm(isRootPath ? 1.5 : 0), marginBottom: `0`}}>
              <Link style={{ boxShadow: `none`, color: `inherit`, }} to={`/`}>{title}</Link>
            </h1>
          </div>
          {isRootPath && (
            <div className="header-desc">
              <div className="header-item-inner">
                {data.site.siteMetadata.descriptionLong}
              </div>
            </div>
          )}
        </div>
      </header>

      <main
        style={{
          marginLeft: `auto`,
          marginRight: `auto`,
          maxWidth: rhythm(32),
          padding: `${rhythm(1.5)} ${rhythm(3 / 4)}`,
        }}
      >
        {children}
      </main>

      <footer
        style={{
          marginLeft: `auto`,
          marginRight: `auto`,
          maxWidth: rhythm(30),
          padding: `${0} ${rhythm(3 / 4)} ${rhythm(2)}`,
        }}
      >
         {new Date().getFullYear()},
        {` `}
        <a href={copyright.url}>{copyright.name}</a>
      </footer>
    </>
  )
}

export default Layout
