import Typography from "typography"

const typography = new Typography({
  baseFontSize: '16px',
  baseLineHeight: 1.53,
  headerFontFamily: ['Open Sans', 'sans-serif'],
  bodyFontFamily: ['Open Sans', 'sans-serif'],
  overrideThemeStyles: ({rhythm}, options) => ({
    'html': {
        backgroundColor: "#f0f5f9",
    },
    'a': {
        color: "#52616b",
        textDecoration: "none",
    },
    'main a:hover': {
        textDecoration: "underline"
    },
    '.header-title': {
        backgroundColor: "#1e2022",
    },
    '.header-title h1': {
        fontFamily: 'Montserrat, sans-serif',
        fontWeight: 100,
    },
    '.header-desc': {
        //fontFamily: 'Montserrat, sans-serif',
        fontWeight: 300,
        backgroundColor: "#52616b"
    },
    '.header-item-inner': {
        overflow: "hidden",
        margin: "auto",
        color: "white",
        maxWidth: rhythm(32),
        padding: `${rhythm(1/3)} ${rhythm(3/4)}`
    },
    '#article-index article': {
        overflow: "hidden",
        borderBottom: "1px #52616b solid",
        marginBottom: rhythm(1)
    },
    '.article-cover, #pnnl-logo': {
        float: "right",
        marginLeft: rhythm(1),
    },
    '.article-cover img': {
        verticalAlign: "middle"
    },
    'article h1': {
        paddingBottom: `${rhythm(1/3)}`,
        borderBottom: 'dashed #555 1px',
        marginBottom: `${rhythm(2/3)}`,
    },
    '#pnnl-logo img': {
        maxHeight: rhythm(3),
        verticalAlign: "middle",
        marginBottom: 0
    },
    'figcaption': {
        textAlign: 'center',
        marginTop: rhythm(1/4),
        marginBottom: rhythm(1),
        fontSize: '16px',
    },
    'table p, table tt': {
        fontSize: 'inherit',
        marginBottom: 0,
        marginBlockStart: 0,
        marginBlockEnd: 0,
    }
  })
})

// Hot reload typography in development.
if (process.env.NODE_ENV !== `production`) {
  typography.injectStyles()
}

export default typography
export const rhythm = typography.rhythm
export const scale = typography.scale
